$card-body-font-size: 14px;
$state-font-size: 12px;

.dashboard {
  width: 100%; }

.notice-list {
  width: 100%;

  .notice {
    color: #A6690C;
    background-color: #FEFCEC;

    .days {
      color: #F86E21;
      font-weight: bold; } } }

.slot-groups {
  width: 100%;

  .group-name {
    width: 90%;
    text-align: center;
    margin: 0 auto;
    padding: 5px 0;
    background-color: #D8D8D8;
    font-size: 1rem;
    border-radius: 5px;
    color: black; }

  .slots {
    width: 100%;

    .slot-card-highlight {
      background-color: #F9EBAF !important;
      box-shadow: #FADE46 0 0 5px;

      .card-header {
        background-color: #E2E2A4 !important; } }

    .slot-card-warn, .slot-card-offline {
      background-color: #F9F7F2 !important;

      .card-header {
        background-color: #E5E5E5 !important;

        .state {
          color: #959595 !important;

          .dot {
            background-color: #959595 !important; } } } }

    .slot-card-disable {
      background-color: #F9F7F2 !important;

      .card-header {
        background-color: #F7E4E4 !important;

        .state {
          color: #D27158 !important;

          .dot {
            background-color: #D27158 !important; } } } }

    .slot-card {
      width: 49%;
      margin-top: 10px;
      background-color: #F9F7F2;
      border-radius: 5px;

      .card-header {
        width: 100%;
        background-color: #D5E9E0;
        border-radius: 5px 5px 0 0;

        .slot-no {
          padding-left: 10px; }

        .state {
          padding-right: 5px;
          color: #4CA086;
          font-size: $state-font-size;
          line-height: 16px;

          .dot {
            float: left;
            margin: 4px;
            display: inline-block;
            background-color: #4CA086;
            width: 10px;
            height: 10px;
            border-radius: 50%; } } }

      .card-body {
        width: 100%;
        height: 50px;

        .left {
          width: 70%;
          padding-left: 10px;

          .sku-no {
            font-size: $card-body-font-size;
            line-height: 16px;
            color: #757472; }

          .sku-name {
            font-size: $card-body-font-size;
            word-break: break-word; } }

        .right-incredible {
          .pcs-value {
            color: #E31C1C !important; }

          .pcs-unit {
            color: #E31C1C !important; } }

        .right {
          width: 29%;

          .pcs-value {
            line-height: 50px;
            font-size: 1.5rem;
            color: black;
            font-weight: bold; }

          .pcs-unit {
            font-size: 10px;
            color: #B2B1AD; } }

        .delimiter {
          width: 1%;
          height: 60%;
          border-left: 1px solid #E3E2E1; } } } } }
